body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}
/* ----------- antd layout样式重定义：开始 ----------- */
.ant-layout {
  height: 100vh;
}
.ant-layout .site-layout-background {
  height: 100%;
}
/* ----------- antd layout样式重定义：结束 ----------- */
/* ----------- antd Message样式重定义：开始 ----------- */
.ant-message .ant-message-notice .ant-message-notice-content {
  border-radius: 0;
}
.ant-message .message_success .ant-message-notice-content {
  border-left: 4px solid #52c41a;
}
.ant-message .message_success .ant-message-notice-content .anticon {
  color: #52c41a;
  font-size: 18px;
}
.ant-message .message_success .ant-message-notice-content span {
  color: #52c41a;
}
.ant-message .message_warning .ant-message-notice-content {
  border-left: 4px solid #ffdd59;
}
.ant-message .message_warning .ant-message-notice-content .anticon {
  color: #ffdd59;
  font-size: 18px;
}
.ant-message .message_warning .ant-message-notice-content span {
  color: #ffdd59;
}
.ant-message .message_error .ant-message-notice-content {
  border-left: 4px solid #EA2027;
}
.ant-message .message_error .ant-message-notice-content .anticon {
  color: #EA2027;
  font-size: 18px;
}
.ant-message .message_error .ant-message-notice-content span {
  color: #EA2027;
}
/* ----------- antd Message样式重定义：结束 ----------- */
