        margin-bottom: 16px;

        .btns-content{
            display: flex;
            justify-content: flex-end;
        }

    }


    .card-block{
        margin-bottom: 16px;

        .desc-label{
            margin-bottom: 4px;
            color: #333;
        }
        .desc-content{
            margin: 0;
            color: #666;
            text-indent: 28px;
        }

        &:last-child{
