.work-bench-div{

    .layout-top,.block{
        margin-bottom: 16px;

        .layout-btns{
            .ant-btn:first-child{
                margin-right: 16px;
            }
        }

        .data-picker{
            width: 100%;
        }
    }


    .layout-markdown{
        .edit-markdown{}

        .show-markdown{
            padding: 4px 11px;
            height: 100%;
            border: 1px solid #e5e5e5;
            border-radius: 2px;

            h1,h2,h3, h4,h5,h6 {
                position: relative;
                margin-top: 1rem;
                margin-bottom: 1rem;
                font-weight: bold;
                line-height: 1.4;
                cursor: text;
            }

            h1 {
                font-size: 30px;
            }

            h2 {
                font-size: 28px;
                border-bottom: 1px solid #cbcbcb;
            }

            h3 {
                font-size: 24px;
            }

            h4 {
                font-size: 18px;
            }

            h5 {
                font-size: 16px;
            }

            h6 {
                font-size: 14px;
                color: #777;
            }

            pre {
                display: block;
                background-color: #f0f0f0;
                padding: 5px;
                border-radius: 2px;
                code {
                    color: #000;
                    background-color: #f0f0f0;
                }

            }

            code {
                background-color: #f0f0f0;
                border-radius: 5px;
                margin: 0px 3px;
                color: #ff502c;
            }

            blockquote {
                padding: 10px 10px 10px 16px;
                border-left: 4px solid #42b983;
                color: #777;
                background-color: rgba(66, 185, 131, .1);
                p{
                    margin: 0;
                }
            }


        }

    }


    
}