.ant-badge {
  margin-right: 60px;
  margin-bottom: 40px;
}
.ant-badge .dtag-content {
  padding: 0 40px;
  display: inline-block;
  color: #fff;
  height: 36px;
  line-height: 36px;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  border-radius: 4px;
}
.ant-popover-inner-content .pop-operating {
  display: flex;
  justify-content: space-around;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.ant-popover-inner-content .pop-operating .ant-btn {
  font-size: 12px;
}
