.article-list-div .filter-seach {
  margin: 0 -16px;
  margin-bottom: 16px;
  border-bottom: 16px solid #f0f0f0;
}
.article-list-div .filter-seach h3 {
  margin: 0;
  padding: 0 16px;
  padding-bottom: 10px;
  font-weight: 600;
  border-bottom: 1px solid #f5f5f5;
}
.article-list-div .filter-seach .filter-items {
  padding: 16px;
}
.article-list-div .table-btns {
  display: flex;
  justify-content: flex-end;
}
.article-list-div .href_block {
  color: #1890ff;
  font-size: 14px;
  cursor: pointer;
}
.article-list-div .introduce {
  margin: 0;
  display: inline-block;
  max-width: 100px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.article-list-div .pages-content {
  display: flex;
  justify-content: flex-end;
}
