.work-bench-div .layout-top,
.work-bench-div .block {
  margin-bottom: 16px;
}
.work-bench-div .layout-top .layout-btns .ant-btn:first-child,
.work-bench-div .block .layout-btns .ant-btn:first-child {
  margin-right: 16px;
}
.work-bench-div .layout-top .data-picker,
.work-bench-div .block .data-picker {
  width: 100%;
}
.work-bench-div .layout-markdown .show-markdown {
  padding: 4px 11px;
  height: 100%;
  border: 1px solid #e5e5e5;
  border-radius: 2px;
}
.work-bench-div .layout-markdown .show-markdown h1,
.work-bench-div .layout-markdown .show-markdown h2,
.work-bench-div .layout-markdown .show-markdown h3,
.work-bench-div .layout-markdown .show-markdown h4,
.work-bench-div .layout-markdown .show-markdown h5,
.work-bench-div .layout-markdown .show-markdown h6 {
  position: relative;
  margin-top: 1rem;
  margin-bottom: 1rem;
  font-weight: bold;
  line-height: 1.4;
  cursor: text;
}
.work-bench-div .layout-markdown .show-markdown h1 {
  font-size: 30px;
}
.work-bench-div .layout-markdown .show-markdown h2 {
  font-size: 28px;
  border-bottom: 1px solid #cbcbcb;
}
.work-bench-div .layout-markdown .show-markdown h3 {
  font-size: 24px;
}
.work-bench-div .layout-markdown .show-markdown h4 {
  font-size: 18px;
}
.work-bench-div .layout-markdown .show-markdown h5 {
  font-size: 16px;
}
.work-bench-div .layout-markdown .show-markdown h6 {
  font-size: 14px;
  color: #777;
}
.work-bench-div .layout-markdown .show-markdown pre {
  display: block;
  background-color: #f0f0f0;
  padding: 5px;
  border-radius: 2px;
}
.work-bench-div .layout-markdown .show-markdown pre code {
  color: #000;
  background-color: #f0f0f0;
}
.work-bench-div .layout-markdown .show-markdown code {
  background-color: #f0f0f0;
  border-radius: 5px;
  margin: 0px 3px;
  color: #ff502c;
}
.work-bench-div .layout-markdown .show-markdown blockquote {
  padding: 10px 10px 10px 16px;
  border-left: 4px solid #42b983;
  color: #777;
  background-color: rgba(66, 185, 131, 0.1);
}
.work-bench-div .layout-markdown .show-markdown blockquote p {
  margin: 0;
}
