.index-div .ant-layout-sider .logo {
  padding: 0 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  height: 64px;
  text-align: center;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  background: rgba(255, 255, 255, 0.2);
}
.index-div .ant-layout-sider .logo p {
  margin: 0;
}
.index-div .ant-layout-sider .logo p.logo-title {
  font-family: fantasy;
  color: #e5e5e5;
  font-size: 26px;
  text-align: center;
}
.index-div .ant-layout-sider .logo p.logo-text {
  color: #a5a5a5;
  font-size: 12px;
}
.index-div .ant-layout .ant-layout-header .header-right {
  padding: 0 16px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.index-div .ant-layout .ant-layout-header .header-right .oprater {
  margin-left: 10px;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.index-div .ant-layout .ant-layout-content .site-layout-background {
  height: calc(100% - 60px);
  overflow-y: auto;
  background: #fff;
  /* ------ 主体视图滚动条样式：开始 */
  /* ------ 主体视图滚动条样式：结束 */
}
.index-div .ant-layout .ant-layout-content .site-layout-background::-webkit-scrollbar {
  /*滚动条整体样式*/
  width: 4px;
  /*高宽分别对应横竖滚动条的尺寸*/
  height: 4px;
}
.index-div .ant-layout .ant-layout-content .site-layout-background::-webkit-scrollbar-thumb {
  /*滚动条里面小方块*/
  border-radius: 5px;
  box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
  background: #778ca3;
}
.index-div .ant-layout .ant-layout-content .site-layout-background::-webkit-scrollbar-track {
  /*滚动条里面轨道*/
  box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
  border-radius: 0;
  background: #d1d8e0;
}
.index-div .site-layout-sub-header-background {
  background: #fff;
}
