.index-div {
    // 右侧: 导航
    .ant-layout-sider{
        // logo
        .logo {
            padding: 0 10px;
            display: flex;
            flex-direction: column;
            justify-content: space-evenly;
            height: 64px;
            text-align: center;
            -webkit-user-select: none;
               -moz-user-select: none;
                -ms-user-select: none;
                    user-select: none;
            background: rgba(255, 255, 255, 0.2);
            p{
                margin: 0;
                &.logo-title{
                    font-family: fantasy;
                    color: #e5e5e5;
                    font-size: 26px;
                    text-align: center;
                }
                &.logo-text{
                    color: #a5a5a5;
                    font-size: 12px;
                }
            }
        }

    }

    // 左侧：内容视图
    .ant-layout{
        .ant-layout-header{
            .header-right{
                padding: 0 16px;
                display: flex;
                justify-content: flex-end;
                align-items: center;
                .avater{}
                .oprater{
                    margin-left: 10px;
                    cursor: pointer;
                    -webkit-user-select: none;
                       -moz-user-select: none;
                        -ms-user-select: none;
                            user-select: none;
                }
            }
        }
        .ant-layout-content{
            .site-layout-background{
                height: calc(100% - 60px);
                overflow-y: auto;
                background: #fff;

                /* ------ 主体视图滚动条样式：开始 */
                &::-webkit-scrollbar {
                    /*滚动条整体样式*/
                    width: 4px;
                    /*高宽分别对应横竖滚动条的尺寸*/
                    height: 4px;
                }
                &::-webkit-scrollbar-thumb {
                    /*滚动条里面小方块*/
                    border-radius: 5px;
                    box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
                    background: #778ca3;
                }

                &::-webkit-scrollbar-track {
                    /*滚动条里面轨道*/
                    box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
                    border-radius: 0;
                    background: #d1d8e0;
                }
                /* ------ 主体视图滚动条样式：结束 */
            }
        }
        
        .ant-layout-footer{
            
        }
    }


    

    
    .site-layout-sub-header-background {
        background: #fff;
    }

    
}